import React, { ChangeEvent, FormEvent } from 'react';
import axios from 'axios';


const ContactPage = (): React.ReactElement => {
  const [emailAddress, setEmailAddress] = React.useState<string>("");
  const [message, setMessage] = React.useState<string>("");

  const onFormSubmitted = async (event: FormEvent): Promise<void> => {
    event.preventDefault()
    const response = await axios({
      url: 'https://formspree.io/f/mleovkjo',
      method: 'post',
      headers: {
        'Accept': 'application/json'
      },
      data: {
        email: emailAddress,
        message: message,
      }
    })
    if (response.status < 400) {
      setEmailAddress("");
      setMessage("");
    }
    window.open(process.env.PUBLIC_URL + '/', "_self");
  };

  const onEmailAddressChanged = (event: ChangeEvent<HTMLInputElement>) => {
    setEmailAddress(event.target.value);
  };

  const onMessageChanged = (event: ChangeEvent<HTMLTextAreaElement>) => {
    setMessage(event.target.value);
  };

  // console.log("emailAddress", emailAddress);
  return (
    // <!-- Root element for center items -->
    <div className="flex flex-col h-screen bg-gray-100">
      {/* <!-- Auth Card Container --> */}
      <div className="grid place-items-center mx-2 my-20 sm:my-auto">
        {/* <!-- Auth Card --> */}
        <div className="w-11/12 p-12 sm:w-8/12 md:w-6/12 lg:w-5/12 2xl:w-4/12 px-6 py-10 sm:px-10 sm:py-6 bg-white rounded-2xl shadow-md lg:shadow-lg">

          {/* <!-- Card Title --> */}
          <h2 className="text-center font-semibold text-3xl mt-4 lg:text-3xl text-gray-800">
            Contact Me
            </h2>

          <form className="mt-10" onSubmit={onFormSubmitted}>
            {/* <!-- Email Input --> */}
            <label className="block text-xs font-semibold text-gray-600 uppercase">
              E-mail
              <input type="email" placeholder="Email" autoComplete="email"
                value={emailAddress}
                onChange={onEmailAddressChanged}
                className="block w-full py-2 px-1 mt-2 text-base text-gray-800 appearance-none border-b-2 border-gray-100 focus:text-gray-500 focus:outline-none focus:border-gray-200"
                required
              />
            </label>

            {/* <label htmlFor="message" className="block mt-2 text-xs font-semibold text-gray-600 uppercase">Message</label>
            <input id="message" type="password" name="message" placeholder="Password" autoComplete="current-password"
              value={message}
              onChange={onMessageChanged}
              className="block w-full py-3 px-1 mt-2 mb-4 text-gray-800 appearance-none border-b-2 border-gray-100 focus:text-gray-500 focus:outline-none focus:border-gray-200"
              required /> */}
            <label htmlFor="message" className="block mt-8 text-xs font-semibold text-gray-600 uppercase">
              Message
              <textarea
                // rows={1}
                placeholder="Message"
                onChange={onMessageChanged}
                value={message}
                className="resize-none block w-full py-2 px-1 mt-2 mb-4 text-base text-gray-800 appearance-none border-b-2 border-gray-100 focus:text-gray-500 focus:outline-none focus:border-gray-200"
              />
            </label>

            {/* <!-- Submit Button --> */}
            <button type="submit" className="w-full py-3 my-2 bg-gray-800 rounded-sm font-medium text-white uppercase focus:outline-none hover:bg-gray-700 hover:shadow-none">
              Submit
            </button>

            {/* <!-- Another Auth Routes --> */}
            {/* <div className="sm:flex sm:flex-wrap mt-8 sm:mb-4 text-sm text-center">
              <a href="forgot-password" className="flex-2 underline">Forgot password?</a>
              <p className="flex-1 text-gray-500 text-md mx-4 my-1 sm:my-auto">or</p>
              <a href="register" className="flex-2 underline">Create an Account</a>
            </div> */}
          </form>
        </div>
      </div>
    </div>
  );
};

export default ContactPage;
