import * as React from 'react';
import { Route, Switch } from 'react-router-dom';
import HomePage from './pages/homePage';
import NotFoundPage from './pages/404Page';
import ProjectsPage from './pages/projectsPage';
import ContactsPage from './pages/contactPage';
import AlankaarsPrivacyPolicy from './pages/alankaarsPrivacyPolicy';

import { usePageTracking } from './util/usePageTracking';


const App = () => {
  usePageTracking();
  return (
    <Switch>
      <Route path={process.env.PUBLIC_URL + '/'} exact={true} component={HomePage} />
      <Route path={process.env.PUBLIC_URL + '/contact'} component={ContactsPage} />
      <Route path={process.env.PUBLIC_URL + '/projects/alankaars/privacy-policy'} component={AlankaarsPrivacyPolicy} />
      <Route path={process.env.PUBLIC_URL + '/projects'} component={ProjectsPage} />
      <Route path='*' component={NotFoundPage} />
    </Switch>
  )
};

export default App;
